import React from 'react'
import Link from 'gatsby-link'
import Helmet from "react-helmet";
import PostListItem from '../../../components/PostListItem'

const LinuxAdmin = ( { data } ) => (
  <div className="container card">
  <Helmet title={ 'Linux Administration - ' + data.site.siteMetadata.title } />
  <div className="row">
      <div className="col-12">
        <h1 className="center">Showing contents of: Linux Administration</h1>
        <hr className="separator"/>
      </div>
    </div>
    <div className="row">
      <div className="col-12">
        <p>Linux Administration is a basic skill for anyone doing deploys to production. Here you can find some tricks to master this discipline</p>
      </div>
    </div>
    <div className="row">
      <div className="col-12">

<ol className="post-list">
      {data.allMarkdownRemark.edges.map(({ node }) =>
        <PostListItem 
        id={node.id}
        path={"/journal/"+ node.frontmatter.category +node.frontmatter.path}
        title={node.frontmatter.title}
        date={node.frontmatter.date}
        foreword={node.frontmatter.foreword}
      />
      )}
</ol>
      </div>
    </div>
  </div>
)

export default LinuxAdmin

export const query = graphql
`query WebBlogQuery {
  site {
    siteMetadata {
      title
    }
  }
  allMarkdownRemark(sort: {order: DESC, fields: [frontmatter___date]} 
    filter: {frontmatter: { category: { eq: "linux-administration" } }} ) {
    edges {
      node {
        id
        timeToRead
        frontmatter {
          title
          date(formatString: "MMMM DD, YYYY")
          path
          category
          foreword
        }
        excerpt
      }
    }
  }
}`


